.content{
    padding-top: 0 !important;
}
.header, .footer{
    display: none;
}
.body{
    background: #1F2032;
    font-family: 'Mabry Pro';
}
._text{
    font-weight: 400;
    color: #F7F7FA;
}
.welcome{
    display: flex;
    flex-wrap: wrap;
    & ._txt h1{
        font-size: 36px;
        letter-spacing: -0.02em;
        margin-bottom: 20px;

    }
    & ._txt p{
        font-size: 15px;
        line-height: 23px;
        letter-spacing: 0em;

    }
    & ._right_block{
        position: relative;
        width: 100%;
        & ._img{
            margin-top: 29px;

            & ._open__vacancies{
                width: 152px;
                height: 152px;
                border-radius: 50%;
                background-color: #fff;
                padding: 0 18px;
                text-align: center;
                display: block;
                color: #312DFF;
                text-decoration: none;
                padding-top: 51px;
                font-size: 15px;
                line-height: 19px;
                letter-spacing: -0.01em;
                z-index: 1;

                & svg {
                    width: 21px;
                    height: 10px;
                    margin: 10px auto 0;
                }
            }



        }
        & ._hand{
            position: absolute;
            top: -69%;
            right: -18%;
            width: 129%;
        }

    }
    & ._photo{
        margin: 40px 0 0;
        & ._welcome_photo1{
            width: 100%;


        }
        & ._welcome_photo2, ._welcome_photo3{
            display: none;
        }
    }

}

.advantages{
    padding-top: 36px;
    & h2{
        font-size: 30px;
        line-height: 36px;
        letter-spacing: -0.01em;
        margin-bottom: 20px;
        width: 70%;
    }
    & ._item{
        background: rgba(38, 39, 59, 0.75);
        margin: 0 -25px;
        padding: 18px 25px 22px ;
        margin-top: 10px;
        & ._title{
            display: flex;
            align-items: center;

            & h3{
                font-size: 19px;
                letter-spacing: -0.02em;
                padding-left: 2px;
            }
            & ._icon{
                width: 34px;
                height: 30px;
                overflow: hidden;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                & ._arrow{
                    position: absolute;
                    left: -74%;
                    top: -128%;
                }
            }
        }
        & p{
            font-size: 13px;
            color: #B0B0C5;
            letter-spacing: 0.01em;
            margin-top: 7px;
        }

    }
    & ._info{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        & ._about{
            margin-bottom: 40px;


            & span{
                font-size: 64px;
                & span{
                    font-size: 42px;
                }
            }
            & p{
                font-size: 12px;
                margin-top: 0;
                max-width: 140px;
                word-wrap: break-word;
            }
        }


    }
    & ._photo{
        margin: 20px -25px 0;
        & ._photo1 {
            width: 100%;
            & img{
                width: 100%;
            }
        }
        & ._photo2{
            display: none;
        }
    }
}
.our_clients{
    margin-top: 40px;
    & h2{
        font-size: 30px;
    }
    & ._clients_items{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        margin-top: 34px;
        & ._item{
            flex-basis: 40%;
            margin-bottom: 30px;
            & ._img{
                min-height: 50px;
                & svg{
                    height: 100% !important;
                }
            }
            & ._txt{
                margin-top: 25px;
                & p{
                    font-size: 13px;
                    color: #9A9AAD;
                    line-height: 16px;
                    letter-spacing: 0.02em;
                }
            }
        }
        & ._star{
            & ._img{
                width: 51px;
                height: 59px;
            }
        }
    }
}
.join_team{
    margin-top: 40px;
    & h2 {
        text-align: center;
        font-size: 30px;
        line-height: 33px;
        letter-spacing: -0.01em;
        width: 75%;
        margin: 0 auto;
    }
    & ._title_p{
        font-size: 14px;
        line-height: 21px;
        letter-spacing: 0.01em;
        color: #9A9AAD;
        text-align: center;
        margin-top: 16px;
        z-index: 4;
    }
    & ._photo {

        margin: 10px 0 0 0;
        position: relative;
        & img{
            width: 100%;
        }
        & ._photo_bg{
            position: absolute;
            z-index: 1;
            width: 100%;
            height: 100%;
            background: linear-gradient(180deg, #1F2032 0%, rgba(31, 32, 50, 0.991353) 6.12%, rgba(31, 32, 50, 0.96449) 12.24%, rgba(31, 32, 50, 0.91834) 18.36%, rgba(31, 32, 50, 0.852589) 24.48%, rgba(31, 32, 50, 0.768225) 30.6%, rgba(31, 32, 50, 0.668116) 36.71%, rgba(31, 32, 50, 0.557309) 42.83%, rgba(31, 32, 50, 0.442691) 48.95%, rgba(31, 32, 50, 0.331884) 55.07%, rgba(31, 32, 50, 0.231775) 61.19%, rgba(31, 32, 50, 0.147411) 67.31%, rgba(31, 32, 50, 0.0816599) 73.43%, rgba(31, 32, 50, 0.03551) 79.55%, rgba(31, 32, 50, 0.0086472) 85.67%, rgba(31, 32, 50, 0) 91.79%);
        }
        & ._vacancies{
            background-color: #fff;
            position: absolute;
            top: -70%;
            bottom: 0;
            right: 0;
            left: 0;
            margin: auto;
            box-sizing: border-box;
            width: 300px;
            height: 72px;
            border-radius: 16px;
            padding: 15px 22px;
            z-index: 1;
            font-size: 16px;
            color: #1F2032;
            text-decoration: none;
            display: flex;
            align-items: center;
            justify-content: space-between;

            & svg{
                width: 20px;
                height: 10px;
            }
        }
    }
}
.fun_work{
    margin-top: 40px;
    & h2 {
        font-size: 28px;
        line-height: 31px;
        letter-spacing: -0.01em;
    }
    & ._items{
        margin-top: 24px;
        display: flex;
        flex-wrap: wrap;
        & ._img{
            border-radius: 16px;
            overflow: hidden;
            width: 100%;
            & img{
                width: 100%;
            }
        }
        & ._txt{
            margin-top: 14px;
            & h3{
                font-size: 19px;
                line-height: 24px;
                letter-spacing: -0.01em;
            }
            & p{
                font-size: 13px;
                line-height: 18px;
                letter-spacing: 0.01em;
                color: #9A9AAD;
                margin-top: 5px;
            }
        }
    }
}

